@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$base-font-family: 'Gotu','Noto Sans JP', sans-serif;
// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $base-font-family;
}

// フォントウェイト
$regular: 400;
$medium: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black: #0D2936;
$black2:#202020;
$white: #fff;
$gray:#bbb;
$green: #408F95;
$light-green:rgba(52, 201, 196, 0.4);
$red:#c94800;
$blue:#265AAD;
$blue2:#04047a;
$blue3:#7599fa;
$light-blue:rgba(182,223,255,1);

